import './index.scss';
import { useTranslation } from 'react-i18next';
import BaseSectionTitle from '../../BaseSectionTitle';
import AboutSmall from '../../AboutSmall';
import aboutBigImg from '../../../assets/images/about-big.png';
import aboutSmallImg from '../../../assets/images/about-small.png';
import smallIcon1 from '../../../assets/images/award.svg';
import smallIcon2 from '../../../assets/images/zap.svg';
import smallIcon3 from '../../../assets/images/user-check.svg';

function MainAbout() {
  const { t } = useTranslation();

  return (
    <div className="main__about">
      <div className="container">
        <div className="main__about__inner d-flex">
          <div className="main__about-left">
            <img src={aboutBigImg} alt="" />
            <div>
              <img src={aboutSmallImg} alt="" />
            </div>
          </div>
          <div className="main__about-right">
            <BaseSectionTitle titleSmall="main.about" titleBig="pga" />
            <p className="mt-1 text-md">
              { t('main.aboutT1') }
            </p>
            <p className="mt-1 text-md">
              { t('main.aboutT2') }
            </p>
            <p className="mt-1 text-md">
              { t('main.aboutT3') }
            </p>
            <button className="main__about-btn mt-3 py-1 px-2 text-sm">{ t('main.more') }</button>
            <div className="main__about__cards d-flex">
              <div className="d-flex align-center"><AboutSmall icon={smallIcon1} text="main.aboutSmallCard1" /></div>
              <div className="d-flex align-center"><AboutSmall icon={smallIcon2} text="main.aboutSmallCard2" /></div>
              <div className="d-flex align-center"><AboutSmall icon={smallIcon3} text="main.aboutSmallCard3" /></div>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}

export default MainAbout;