import "slick-carousel/slick/slick.css";
import './index.scss';
import { useTranslation } from 'react-i18next';
import { useState } from 'react';
import BaseSectionTitle from '../../BaseSectionTitle';
import imgBig from '../../../assets/images/client.png';
import cardIcon1 from '../../../assets/images/crosshair.svg';
import cardIcon2 from '../../../assets/images/smile.svg';
import cardIcon3 from '../../../assets/images/briefcase.svg';
import arrowRight from '../../../assets/images/arrow-right2.svg';

import Slider from "react-slick";

function PrevArrow({ className, style, onClick }) {
  return (
    <button
      className={`${className} prev-btn`}
      style={{style}}
      onClick={onClick}
    >
      <img src={arrowRight} alt="" />
    </button>
  );
}

function NextArrow({ className, style, onClick }) {
  return (
    <button
      className={`${className} next-btn`}
      style={{style}}
      onClick={onClick}
    >
      <img src={arrowRight} alt="" />
    </button>
  );
}

function MainResponses() {
  const { t } = useTranslation();
  
  const [slider1, setSlider1] = useState(null);
  const [slider2, setSlider2] = useState(null);

  return (
    <div className="main__responses">
      <div className="container">
        <div className="main__responses__inner d-flex">
          
          <div className="main__responses__left">
            <Slider
              asNavFor={slider2}
              ref={el => setSlider1(el)}
              arrows={false}
              swipeToSlide={true}
            >
              <img src={imgBig} alt="" />
              <img src={imgBig} alt="" />
              <img src={imgBig} alt="" />
            </Slider>
          </div>
          
          <div className="main__responses__right">
            <div className="main__responses__right-inner">
              <BaseSectionTitle
                titleSmall="main.response"
                titleBig="main.clientsOpinion"
                titleSmallColor="#ffffff"
                titleBigColor="#ffffff"
                borderColor="#ffffff"
              />
              <Slider
                asNavFor={slider1}
                ref={el => setSlider2(el)}
                swipeToSlide={true}
                prevArrow={<PrevArrow />}
                nextArrow={<NextArrow />}
              >
                <div>
                  <div className="clients__name pn-bold">
                    Towney Liakos
                    <span className="pn-light text-sm">
                      { t('main.newYork') }
                    </span>
                  </div>
                  <p className="main__responses-text text-md">
                    { t('main.responseT') }
                  </p>
                </div>
                <div>
                  <div className="clients__name pn-bold">
                    Towney Liakos
                    <span className="pn-light text-sm">
                      { t('main.newYork') }
                    </span>
                  </div>
                  <p className="main__responses-text text-md">
                    { t('main.responseT') }
                  </p>
                </div>
                <div>
                  <div className="clients__name pn-bold">
                    Towney Liakos
                    <span className="pn-light text-sm">
                      { t('main.newYork') }
                    </span>
                  </div>
                  <p className="main__responses-text text-md">
                    { t('main.responseT') }
                  </p>
                </div>
              </Slider>
              <div className="main__responses__cards d-flex align-center">
                <div className="main__responses__card d-flex align-center">
                  <img className="p-2" src={cardIcon1} alt="" />
                  <p className="pn-bold text-sm">
                    <span className="pn-light text-sm">50+</span>
                    { t('main.goal') }
                  </p>
                </div>
                <div className="main__responses__card d-flex align-center">
                  <img className="p-2" src={cardIcon2} alt="" />
                  <p className="pn-bold text-sm">
                    <span className="pn-light text-sm">200+</span>
                    { t('main.happyClients') }
                  </p>
                </div>
                <div className="main__responses__card d-flex align-center">
                  <img className="p-2" src={cardIcon3} alt="" />
                  <p className="pn-bold text-sm">
                    <span className="pn-light text-sm">100+</span>
                    { t('main.projects') }
                  </p>
                </div>
              </div>
            </div>
          </div>
          
        </div>
      </div>
    </div>
  )
}

export default MainResponses;