import './HeaderMenu.scss';
import { useState, useEffect } from 'react';
import { useTranslation } from 'react-i18next';
import { Link } from 'react-router-dom';

function HeaderMenu({ targetId }) {
  const { t, i18n } = useTranslation();

  let directPath = window.location.pathname;
  const path = directPath.split('/');
  let lang = path[path.length - 1];
  if (lang !== 'uz' && lang !== 'ru' && lang !== 'en') {
    lang = localStorage.getItem('lang');
  } else {
    directPath = directPath.slice(0, directPath.length - 3);
  }

  const [isMenuActive, setIsMenuActive] = useState(false);

  const [headerLinks, setHeaderLinks] = useState([
    {label: 'main', link: '/pga', isActive: false, id: 1},
    {label: 'products', link: '/pga/products', isActive: false, id: 2},
    {label: 'about', link: '/pga/about', isActive: false, id: 3},
    {label: 'tubes', link: '/pga/tubes', isActive: false, id: 4},
    {label: 'contact', link: '/pga/contact', isActive: false, id: 5}
  ])

  const [langs, setLangs] = useState([
    {bigLabel: "O'zbekcha", smallLabel: "UZ", isActive: false, id: 1},
    {bigLabel: "", smallLabel: "RU", isActive: false, id: 2},
    {bigLabel: "English", smallLabel: "EN", isActive: false, id: 3}
  ])

  useEffect(() => {
    setTimeout(() => {
      i18n.changeLanguage(lang);
    })
    highlightLang(lang);
  }, [])

  useEffect(() => {
    highlightMenuLink(targetId);
  }, [targetId])

  function handleBurgerClick() {
    setIsMenuActive(!isMenuActive);
    document.querySelector('body').classList.toggle('lock');
  }

  function highlightMenuLink(targetId) {
    if (targetId === 10) {
      targetId = 2;
    }
    setHeaderLinks(
      headerLinks.map(item => {
        return {
          ...item,
          isActive: Number(targetId) === item.id
        }
      })
    )
    closeMenu();
  }

  function handleLangClick(targetLang) {
    i18n.changeLanguage(targetLang);
    localStorage.setItem('lang', targetLang);
    highlightLang(targetLang);
  }

  function highlightLang(lang) {
    setLangs(
      langs.map(item => {
        return {
          ...item,
          isActive: lang === item.smallLabel.toLowerCase()
        }
      })
    )
    closeMenu();
  }

  function closeMenu() {
    setIsMenuActive(false);
    document.querySelector('body').classList.remove('lock');
  }

  return <>
    <a href="tel:+998712625109" className="header__tel">+998 (71) 262-51-09</a>
    <div className={`header__burger ${isMenuActive ? 'active' : ''}`} onClick={handleBurgerClick}>
      <span></span>
    </div>
    <nav className={`header__menu ${isMenuActive ? 'active' : ''}`}>
      <ul className="header__list">
        {
          headerLinks.map((item) => {
            return <li key={`header__link${item.id}`}>
              <Link
                to={item.link}
                className={`header__link ${item.isActive ? 'header__link--active' : ''}`}
                onClick={ () => {closeMenu()} }
              >{t(`header.${item.label}`)}</Link>
            </li>
          })
        }
      </ul>
      <div className="header__langs">
        <ul className={isMenuActive ? 'big' : 'small'}>
          {
            langs.map(lang => {
              return <li
                key={`header__lang${lang.id}`}
                className={lang.isActive ? 'active-lang' : ''}
                onClick={() => handleLangClick(lang.smallLabel.toLowerCase())}
              >
                <Link
                  to={`${directPath}/${lang.smallLabel.toLowerCase()}`}
                >{isMenuActive ? lang.bigLabel : lang.smallLabel}</Link>
              </li>
            })
          }
        </ul>
      </div>
    </nav>
  </>
}

export default HeaderMenu;