import "slick-carousel/slick/slick.css";
import './index.scss';
import Slider from "react-slick";
import BaseSectionTitle from '../BaseSectionTitle';
import partner1 from '../../assets/images/partner-1.png';
import partner2 from '../../assets/images/partner-2.png';
import partner3 from '../../assets/images/partner-3.png';
import partner4 from '../../assets/images/partner-4.png';
import partner5 from '../../assets/images/partner-5.png';
import partner6 from '../../assets/images/partner-6.png';

function Partners({ titleSmall, titleBig }) {
  const sliderSettings = {
    arrows: false,
    slidesToShow: 6,
    slidesToScroll: 6,
    swipeToSlide: true,
    autoplay: true,
    infinite: true,
    responsive: [
      {
        breakpoint: 1210,
        settings: {
          slidesToShow: 4,
          slidesToScroll: 4,
          infinite: true,
        }
      },
      {
        breakpoint: 800,
        settings: {
          slidesToShow: 3,
          slidesToScroll: 3,
          infinite: true,
        }
      },
      {
        breakpoint: 600,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 2,
          infinite: true,
        }
      },
      {
        breakpoint: 400,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
          infinite: true,
        }
      },
    ]
  }

  return (
    <div className="main__partners">
      <div className="container">
        <BaseSectionTitle titleSmall={titleSmall} titleBig={titleBig} />
        <div className="main__partners-slider mt-2">
          <Slider {...sliderSettings}>
            <div className="main__partners-slider-item"><img src={partner1} alt="" /></div>
            <div className="main__partners-slider-item"><img src={partner2} alt="" /></div>
            <div className="main__partners-slider-item"><img src={partner3} alt="" /></div>
            <div className="main__partners-slider-item"><img src={partner4} alt="" /></div>
            <div className="main__partners-slider-item"><img src={partner5} alt="" /></div>
            <div className="main__partners-slider-item"><img src={partner6} alt="" /></div>
            <div className="main__partners-slider-item"><img src={partner1} alt="" /></div>
            <div className="main__partners-slider-item"><img src={partner2} alt="" /></div>
            <div className="main__partners-slider-item"><img src={partner3} alt="" /></div>
            <div className="main__partners-slider-item"><img src={partner4} alt="" /></div>
            <div className="main__partners-slider-item"><img src={partner5} alt="" /></div>
            <div className="main__partners-slider-item"><img src={partner6} alt="" /></div>
          </Slider>
        </div>
      </div>
    </div>
  )
}

export default Partners;