import './index.scss';
import { useTranslation } from 'react-i18next';
import icon1 from '../../assets/images/diameter.png';
import icon2 from '../../assets/images/pressure.png';
import icon3 from '../../assets/images/atoms.svg';
import icon4 from '../../assets/images/sdr.png';


function ProductsCard({ card }) {
  const { t } = useTranslation();

  return (
    <div className="products-card d-flex">
      <img src={card.img} alt="Product" />
      <div className="products-card__info">
        <div>
          <h4 className="mb-1 pn-bold text-md">{ t(card.title) }</h4>
          <p className="products-card__text pn-light">
            { t(card.description) }
          </p>
        </div>
        <div className="products-card__specifications d-flex">
          <div className="products-card__specifications-item d-flex">
            <div className="products-card__specifications-item-img">
              <img src={icon1} alt="Icon" />
            </div>
            <p className="pn-semibold">
              { t('main.diameter') }
              <span className="pn-light text-sm">{card.diameter}</span>
            </p>
          </div>
          <div className="products-card__specifications-item d-flex">
            <div className="products-card__specifications-item-img">
              <img src={icon2} alt="Icon" />
            </div>
            <p className="pn-semibold">
              { t('main.workingPressure') }
              <span className="pn-light text-sm">{card.pressure}</span>
            </p>
          </div>
          <div className="products-card__specifications-item d-flex">
            <div className="products-card__specifications-item-img">
              <img src={icon3} alt="Icon" />
            </div>
            <p className="pn-semibold">
              { t('main.material') }
              <span className="pn-light text-sm">{`${t('main.materialPe')} ${card.material}`}</span>
            </p>
          </div>
          <div className="products-card__specifications-item d-flex">
            <div className="products-card__specifications-item-img">
              <img src={icon4} alt="Icon" />
            </div>
            <p className="pn-semibold">
              { t('main.sdr') }
              <span className="pn-light text-sm">{card.sdr}</span>
            </p>
          </div>
        </div>
      </div>
    </div>
  )
}

export default ProductsCard;