import './index.scss';
import { useTranslation } from 'react-i18next';

function NewsMailingForm() {
  const { t } = useTranslation();

  return (
    <div className="footer__email">
      <div className="footer__email__inner">
        <div className="footer__email-left">
          <img alt="example" src="data:image/png;base64," />
          <h3 className="pn-bold">{ t('subscribeT1') }<br />{ t('subscribeT2') }</h3>
        </div>
        <div className="footer__email-right">
          <input placeholder={ t('emailPlaceholder') } type="text" />
          <button type="button">
            <span className="text-md">{ t('subscribe') }</span>
          </button>
        </div>
      </div>
    </div>
  )
}

export default NewsMailingForm;