import { useEffect, useContext } from 'react';
import { MenuContext } from '../../App';
import RouterComponent from '../../components/RouterComponent';
import MainTop from '../../components/Main/MainTop';
import MainAbout from '../../components/Main/MainAbout';
import MainTopProducts from '../../components/Main/MainTopProducts';
import MainLatestProjects from '../../components/Main/MainLatestProjects';
import MainQualityIndicators from '../../components/Main/MainQualityIndicators';
import MainResponses from '../../components/Main/MainResponses';
import MainLatestNews from '../../components/Main/MainLatestNews';
import Partners from '../../components/Partners';

function Main() {
  const setActiveMenuLink = useContext(MenuContext);

  useEffect(() => {
    setActiveMenuLink(1);
    window.scrollTo({top: 0, left: 0, behavior: "smooth"});
  }, [])

  return (
    <>
      <RouterComponent>
        <MainTop />
        <MainAbout />
        <MainTopProducts />
        <MainLatestProjects />
        <MainQualityIndicators />
        <MainResponses />
        <MainLatestNews />
        <Partners titleSmall="main.collaboration" titleBig="main.ourPartners" />
      </RouterComponent>
    </>
  )
}

export default Main;