import './index.scss';
import { useTranslation } from 'react-i18next';
import arrowsRight from '../../../assets/images/arrows-right.svg';

function MainTopCard({ icon, title, description, btn }) {
  const { t } = useTranslation();

  return (
    <div className="main__top-card d-flex p-3">
      <div>
        <div className="main__top-card__title d-flex align-center">
          <img src={icon} alt="" />
          <p className="text-md pn-bold">
            { t(`main.${title}`) }
          </p>
        </div>
        <p className="main__top-card__description my-1 text-sm pn-light">
          { t(`main.${description}`) }
        </p>
      </div>
      <a className="main__top-card__link text-sm" href="#">
        { t(`main.${btn}`) }
        <img src={arrowsRight} alt="" />
      </a>
    </div>
  )
}

export default MainTopCard;