import './index.scss';
import { useTranslation } from 'react-i18next';
import BaseSectionTitle from '../../BaseSectionTitle';
import imgBig from '../../../assets/images/quality-indicators.png';
import cardIcon1 from '../../../assets/images/activity.svg';
import cardIcon2 from '../../../assets/images/check-circle.svg';
import cardIcon3 from '../../../assets/images/alert-triangle.svg';

function MainQualityIndicators() {
  const { t } = useTranslation();

  return (
    <div className="main__quality-indicators">
      <div className="container">
        <div className="main__quality-indicators__inner d-flex">
          <div className="main__quality-indicators__left">
            <div className="main__quality-indicators__left-inner">
              <BaseSectionTitle
                titleSmall="main.productsQuality"
                titleBig="main.qualityIndicators"
                titleSmallColor="#ffffff"
                titleBigColor="#ffffff"
                borderColor="#ffffff"
              />
              <p className="main__quality-indicators-text text-md">
                { t('main.qualityT') }
              </p>
              <div className="main__quality-indicators__cards d-flex align-center">
                <div className="main__quality-indicators__card d-flex align-center">
                  <img className="p-2" src={cardIcon1} alt="" />
                  <p>
                    { t('main.qualitySmallCard1') }
                  </p>
                </div>
                <div className="main__quality-indicators__card d-flex align-center">
                  <img className="p-2" src={cardIcon2} alt="" />
                  <p>
                    { t('main.qualitySmallCard2') }
                  </p>
                </div>
                <div className="main__quality-indicators__card d-flex align-center">
                  <img className="p-2" src={cardIcon3} alt="" />
                  <p>
                    { t('main.qualitySmallCard3') }
                  </p>
                </div>
              </div>
            </div>
          </div>
          <div className="main__quality-indicators__right">
            <img src={imgBig} alt="" />
          </div>
        </div>
      </div>
    </div>
  )
}

export default MainQualityIndicators;