import './index.scss';
import { useTranslation } from 'react-i18next';
import { useState } from 'react';
import BaseSectionTitle from '../../BaseSectionTitle';

import cardPhoto1 from '../../../assets/images/latest-news-1.png';
import cardPhoto2 from '../../../assets/images/latest-news-2.png';
import cardPhoto3 from '../../../assets/images/latest-news-3.png';
import cardPhoto4 from '../../../assets/images/latest-news-4.png';
import cardPhoto5 from '../../../assets/images/latest-news-5.png';
import cardPhoto6 from '../../../assets/images/latest-news-6.png';

function MainLatestProjects() {
  const { t } = useTranslation();

  const [btns, setBtns] = useState([
    {
      text: "main.allProjects",
      isActive: true,
      id: 1
    },
    {
      text: "main.production",
      isActive: false,
      id: 2
    },
    {
      text: "main.polimerGallery",
      isActive: false,
      id: 3
    }
  ]);

  function handleClick(btn) {
    setBtns(
      btns.map(item => {
        return {
          ...item,
          isActive: item.id === btn.id
        }
      })
    )
  }

  return (
    <div className="main__projects">
      <div className="container">
        <div className="main__projects-top d-flex align-center">
          <BaseSectionTitle titleSmall="main.recentlyCompleted" titleBig="main.latestProjects" />
          <ul className="d-flex text-sm">
            {
              btns.map((item) => {
                return (
                  <li key={item.id}>
                    <button
                     className={item.isActive ? 'active' : ''}
                     onClick={() => handleClick(item)}
                    >{ t(item.text) }</button>
                  </li>
                )
              })
            }
          </ul>
        </div>
        <div className="main__projects-cards d-flex">
          <div className="main__projects-card mt-2">
            <img src={cardPhoto1} alt="" />
          </div>
          <div className="main__projects-card mt-2">
            <img src={cardPhoto2} alt="" />
          </div>
          <div className="main__projects-card mt-2">
            <img src={cardPhoto3} alt="" />
          </div>
          <div className="main__projects-card mt-2">
            <img src={cardPhoto4} alt="" />
          </div>
          <div className="main__projects-card mt-2">
            <img src={cardPhoto5} alt="" />
          </div>
          <div className="main__projects-card mt-2">
            <img src={cardPhoto6} alt="" />
          </div>
        </div>
      </div>
    </div>
  )
}

export default MainLatestProjects;