import './index.scss';
import { useTranslation } from 'react-i18next';
import MainTopCard from '../MainTopCard';
import icon1 from '../../../assets/images/sliders.svg';
import icon2 from '../../../assets/images/clock.svg';
import icon3 from '../../../assets/images/users.svg';

function MainTop() {
  const { t } = useTranslation();

  return (
    <>
      <div className="main__top">
        <div className="container">
          <h1 className="main__top-title text-lg">
            <span className="main__top-title-small pn-light text-md">{ t('main.title') }</span>
            <span className="main__top-title-big pn-bold text-xl">POLIMER GROUP ASIA </span>
            { t('main.description') }
          </h1>
          <button className="main__top-btn pn-semibold text-md">{ t('main.more') }</button>
          <div className="main__top-cards">
            <div className="container">
              <div className="main__top-cards__inner d-flex">
                <MainTopCard icon={icon1} title="cardT1" description="cardD1" btn="moreServices" />
                <MainTopCard icon={icon2} title="cardT2" description="cardD2" btn="more" />
                <MainTopCard icon={icon3} title="cardT3" description="cardD3" btn="more" />
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  )
}

export default MainTop;