import './assets/styles/index.scss';
import Main from './pages/Main';
import BaseHeader from './components/BaseHeader';
import BaseFooter from './components/BaseFooter';
import Loader from './components/Loader';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import { I18nextProvider } from 'react-i18next';
import i18n from './i18n';
import React, { useEffect, useState, Suspense } from 'react';

const Products = React.lazy(() => import('./pages/Products'));
const AboutUs = React.lazy(() => import('./pages/AboutUs'));
const ContactUs = React.lazy(() => import('./pages/ContactUs'));
const NotFound = React.lazy(() => import('./pages/NotFound'));
const Tubes = React.lazy(() => import('./pages/Tubes'));

export const MenuContext = React.createContext(null);

function App() {
  const [isLoading, setIsLoading] = useState(true);
  const [activeMenuLink, setActiveMenuLink] = useState(null);

  useEffect(() => {
    async function fetchData() {
      let activeLang = localStorage.getItem('lang');
      if (!activeLang) {
        activeLang = await findLocalLang();
        localStorage.setItem('lang', activeLang);
      }
      setIsLoading(false);
    }

    fetchData();
}, [])

  async function findLocalLang() {
    const data = await fetch('https://ip-geolocation.whoisxmlapi.com/api/v1?apiKey=at_uHhziqi9nuWkqc62dhzcDYxoBHNad');
    const response = await data.json();
    let lang = 'en';

    switch(response.location.country) {
      case 'UZ':
        lang = 'uz';
        break;
      case 'RU':
      case 'KZ':
      case 'KG':
        lang = 'ru';
        break;
      default:
        break;
    }
    return lang;
  }

  return <>
    {
      isLoading ? (<Loader />) : (
        <I18nextProvider i18n={i18n}>
          <Router>
            <MenuContext.Provider value={setActiveMenuLink}>
              <BaseHeader targetId={activeMenuLink} />
              <Suspense fallback={<Loader />}>
                <Switch>
                  <Route exact path={["/pga", "/pga/:lang([a-zA-Z]{2})"]} component={Main} />
                  <Route exact path={["/pga/products", "/pga/products/:lang([a-zA-Z]{2})", "/pga/products/:product/:lang([a-zA-Z]{2})?"]} component={Products} />
                  <Route exact path={["/pga/about", "/pga/about/:lang([a-zA-Z]{2})"]} component={AboutUs} />
                  <Route exact path={["/pga/tubes", "/pga/tubes/:lang([a-zA-Z]{2})"]} component={Tubes} />
                  <Route exact path={["/pga/contact", "/pga/contact/:lang([a-zA-Z]{2})"]} component={ContactUs} />
                  <Route path="/pga/*" component={NotFound} />
                </Switch>
              </Suspense>
              <BaseFooter />
            </MenuContext.Provider>
          </Router>
        </I18nextProvider>
      )
    }
  </>
}

export default App;
