import './index.scss';
import { useTranslation } from 'react-i18next';
import BaseSectionTitle from '../../BaseSectionTitle';
import ProductsCard from '../../ProductsCard';
import arrowRight from '../../../assets/images/arrow-right.svg';
import cardPhoto1 from '../../../assets/images/products-card-1.png';
import cardPhoto2 from '../../../assets/images/products-card-2.png';
import cardPhoto3 from '../../../assets/images/products-card-3.png';
import cardPhoto4 from '../../../assets/images/products-card-4.png';
import cardPhoto5 from '../../../assets/images/products-card-5.png';
import cardPhoto6 from '../../../assets/images/products-card-6.png';

function MainTopProducts() {
  const { t } = useTranslation();

  const cards = [
    {
      title: "main.productsCardT1",
      description: "main.productsCardD1",
      img: cardPhoto1,
      diameter: "50-60",
      pressure: "1.2",
      material: "100",
      sdr: "9; 11; 17; 21; 26",
    },
    {
      title: "main.productsCardT2",
      description: "main.productsCardD2",
      img: cardPhoto2,
      diameter: "50-60",
      pressure: "1.2",
      material: "100",
      sdr: "9; 11; 17; 21; 26",
    },
    {
      title: "main.productsCardT3",
      description: "main.productsCardD3",
      img: cardPhoto3,
      diameter: "50-60",
      pressure: "1.2",
      material: "100",
      sdr: "9; 11; 17; 21; 26",
    },
    {
      title: "main.productsCardT4",
      description: "main.productsCardD4",
      img: cardPhoto4,
      diameter: "50-60",
      pressure: "1.2",
      material: "100",
      sdr: "9; 11; 17; 21; 26",
    },
    {
      title: "main.productsCardT5",
      description: "main.productsCardD5",
      img: cardPhoto5,
      diameter: "50-60",
      pressure: "1.2",
      material: "100",
      sdr: "9; 11; 17; 21; 26",
    },
    {
      title: "main.productsCardT6",
      description: "main.productsCardD6",
      img: cardPhoto6,
      diameter: "50-60",
      pressure: "1.2",
      material: "100",
      sdr: "9; 11; 17; 21; 26",
    }
  ]

  return (
    <div className="main__top-products">
      <div className="container">
        <div className="main__top-products__top d-flex align-center">
          <BaseSectionTitle titleSmall="main.qualityProducts" titleBig="main.topProducts" />
          <button className="main__top-products__btn d-flex align-center pl-3 text-sm">
            { t('main.viewMore') }
            <div className="d-flex justify-center align-center">
              <img src={arrowRight} alt="" />
            </div>
          </button>
        </div>
        <div className="main__top-products__cards d-flex">
          {
            cards.map((card, index) => {
              return (
                <div
                  className="main__top-products__cards-item"
                  key={index}
                ><ProductsCard card={card} /></div>
              )
            })
          }
        </div>
      </div>
    </div>
  )
}

export default MainTopProducts;